<template>
  <v-content>
    <v-container fluid>

      <v-row>
        <v-col>
          <h1 class="headline">Description</h1>
          <v-textarea style="font-family: monospace;"
          name="input-7-1" label="Spécifications JSON"
          v-model="jsonText">
        </v-textarea>
        <v-btn class="ma-2" tile outlined color="warning"
        @click="clear()">
        <v-icon left>mdi-delete</v-icon>Effacer
      </v-btn>
      <v-btn class="ma-2" tile outlined color="info"
      @click="save()">
      <v-icon left>mdi-export</v-icon>Exporter
    </v-btn>
  </v-col>
  <v-col>
    <div ref="mrow">
      <h1 class="headline">Résultats</h1>
      <div v-if="compiled">
        <div v-for="item in obj" :key="item.title">
          <Card v-bind:properties="item"/>
        </div>
      </div>
      <div v-else>
        <div class="text-center">
          <span class="group pa-2">
            <v-icon v-show="!imgShow" color="orange darken-2" x-large>mdi-alert</v-icon>
          </span>
          <p class="orange--text">{{ compilationErrorMsg }}</p>
          <div align="center">
            <v-img v-show="imgShow" width="450" src="../assets/empty.svg"></v-img>
          </div>
</div>
      </div>
    </div>
  </v-col>
</v-row>
</v-container>
</v-content>

</template>

<script>

import Card from './Card.vue';

export default {
  name: 'Speck',
  components: {
    Card,
  },
  data: () => ({
    // JSON specifications
    jsonText: '{"list":[{"title":"montitre"}]}',

    // Parsed JSON object
    obj: {},

    // Display result if true, or error message otherwise
    compiled: true,

    // Display picture when text is empty
    imgShow: true,

    // Error msg
    compilationErrorMsg: '',
  }),
  watch: {
    // Whenever specifications change, this function will run
    jsonText() {
      try {
        // Parse JSON text...
        this.obj = JSON.parse(this.jsonText).list;

        // ...and display result if there's no syntax error
        this.compiled = true;
        this.imgShow = false;
      } catch (error) {
        if (this.jsonText.replace(/\s/g, '').length > 0) {
          // Otherwise, print the error message
          this.compiled = false;
          this.imgShow = false;
          this.compilationErrorMsg = error.message;
        } else {
          this.compiled = false;
          this.imgShow = true;
          this.compilationErrorMsg = '';
        }
      }
    },
  },
  methods: {
    // Removes JSON text
    clear() {
      this.jsonText = '';
    },
  },
};
</script>
